.header{
    display: flex;
    align-items: center;
    height: 60px;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: #131921;
}
.header__logo{
    object-fit: contain;
    width: 100px;
    margin: 10px 20px 0;
}
.header__search{
    display: flex;
    align-items: center;
    flex: 1;
    border-radius: 24px;
}
.header__searchInput{
    width: 100%;
    height: 14px;
    padding: 8px;
    border: none;
}
.header__searchIcon{
    padding: 4px;
    height: 22px !important;
    background-color: #cd9042;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option{
    display: flex;
    flex-direction: column;
    color: white;
    margin: 0 10px 0 10px;
    
}
.header__optionBasket{
    display: flex;
    align-items: center;
    color: white
}
.header__optionLineOne{
    font-size: 12px;
}
.header__optionLineTwo{
    font-size: 16px;
    font-weight: 900; 
}
.header__basketCount{
    margin: 0 10px 0 10px;
}
a,a:hover, a:focus { text-decoration: none; }
