.home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home__row{
    display: flex;
    z-index: 1;
    margin: 0 20px 20px 20px
}
.home__image{
    width: 100%;
    z-index: -1;
    margin-bottom: -220px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0))
}